:root {
  --bg-1-color: #303030;
  --bg-2-color: #4e0537;
  --app-1-color: #5e4961ad;
  --app-2-color: #c402d1;
  --columns-1-color: #f8f6f8;
  --columns-2-color: #8c0898;
  --task-1-color: #bacbed;
  --text: #fff;
  --html-redius: 10px;
}

* {
  border-radius: var(--html-redius);
  font-family: Georgia, emoji;
}

body {
  background-image: linear-gradient(90deg, var(--bg-1-color, #3b1840), var(--bg-2-color));
}

.App {
  text-align: center;
  background: var(--app-1-color);
  border-style: groove;
  border-color: var(--app-2-color);
  color: var(--text);
  border: 12px;
  margin: 23px;
  padding: 10px 10px 100px;
}

.scrum-board {
  border: 12px double, var(--app-2-color);
  color: var(--text);
  flex-direction: column;
  align-items: center;
  padding: 20px;
  display: flex;
}

.add-task-form {
  margin-bottom: 20px;
}

.add-task-form input, .add-task-form select, .add-task-form button {
  border-color: var(--app-2-color);
  margin-right: 10px;
}

.columns {
  justify-content: space-around;
  width: 100%;
  display: flex;
}

.column {
  border: 3px solid var(--app-2-color);
  border-color: var(--columns-1-color);
  width: 30%;
  padding-bottom: 20px;
}

.assignment {
  border: 3px solid var(--task-1-color);
  border-color: var(--task-1-color);
  margin: 10px 0;
  padding: 10px;
}

button {
  margin-top: 10px;
}
